section.admin-sidebar {
  background-color: #fff;
  height: 48px;
  position: relative;
  z-index: 10001;
  border-bottom: 1px solid #d7dce1;
}

/* @media only screen and (min-width: 768px) */
/* section.admin-sidebar .sidebar-logo-wrap {
    margin: 0 auto;
    top: 12px;
} */
section.admin-sidebar .sidebar-logo-wrap {
  margin: 0 0 0 8px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  position: relative;
  top: 4px;
}
.secondary.admin.has-toolbar,
.sidebar-logo-wrap {
  transition: all 0.2s cubic-bezier(0.08, 0.59, 0.29, 0.99);
}
.sidebar-logo-wrap {
  top: 20px;
  display: block;
}
.secondary.admin.has-toolbar,
.sidebar-logo-wrap {
  transition: all 0.2s cubic-bezier(0.08, 0.59, 0.29, 0.99);
}
.sidebar-logo-wrap {
  top: 20px;
  display: block;
}

section.admin-sidebar h1 .logo {
  cursor: pointer;
  display: block;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border-radius: 100%;
}

section.admin-sidebar .sidebar__icons {
  position: absolute;
  bottom: 84px;
  width: 64px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

section.admin-sidebar .sidebar__icons div.nav-item {
  width: 64px;
  height: 48px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
/* @media only screen and (min-width: 768px) */
/* section.admin-sidebar .nav-item {
    width: 64px;
} */
section.admin-sidebar .nav-item {
  position: relative;
  width: 30px;
  padding: 0;
  cursor: pointer;
}
section.admin-sidebar .sidebar__icons--support {
  visibility: visible;
}

