section.admin-sidebar .sub-nav {
  max-height: 60vh;
  display: block;
  background-color: #fff;
  position: absolute;
  right: 50%;
  top: 24px;
  min-width: 245px;
  margin: 0;
  padding: 0;
  border-radius: 8px;
  transition: visibility 0s linear 0.15s,
    opacity 0.15s cubic-bezier(0.08, 0.59, 0.29, 0.99);
  opacity: 0;
  visibility: hidden;
  z-index: 1500;
  box-shadow: 0 0 0 1px hsl(0deg 0% 53% / 10%), 0 15px 35px 0 rgb(49 49 49 / 5%),
    0 5px 15px 0 rgb(0 0 0 / 8%);
}

section.admin-sidebar .sub-nav .divider {
  padding: 10px 12px 5px;
  color: #0a0b0d;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.03em;
}

section.admin-sidebar .sub-nav-item {
  list-style: none;
  list-style-type: none;
}

section.admin-sidebar .sidebar__icons div.nav-item {
  width: 64px;
  height: 48px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
