}

section.admin-sidebar .sidebar__icons--headway #HW_badge_cont {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
}
section.admin-sidebar
  .sidebar__icons--headway
  span:not([data-count-unseen=""]) {
  display: block !important;
}
/* <style> */
.HW_badge_cont.HW_visible {
  visibility: visible;
  pointer-events: auto;
}
/* <style> */
.HW_badge_cont {
  visibility: hidden;
  pointer-events: none;
  display: block;
  cursor: pointer;
  position: relative;
  width: 32px;
  height: 32px;
}

